<template>
  <div class="container">
    <!-- 訊息提示框 -->
    <Alert
      :MODE="MSG.MODE"
      :MSGTYPE="MSG.TYPE"
      :TITLE="MSG.TITLE"
      :TEXT="MSG.TEXT"
      @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
    />
    <Loader /><!-- 資料加載 -->
    <div class="text mt-4">
      <span>階</span>
      <span>段</span>
      <span>測</span>
      <span>驗</span>
    </div>
    <div class="time">
      <span>
        <div id="d">{{ timeCount.days }}</div>
        DAYS
      </span>
      <span>
        <div id="h">{{ timeCount.hours }}</div>
        HOURS
      </span>
      <span>
        <div id="m">{{ timeCount.minutes }}</div>
        MINS
      </span>
      <span>
        <div id="s">{{ timeCount.seconds }}</div>
        SECS
      </span>
    </div>
    <h2 class="text-center mt-4">請登入</h2>
    <form class="row">
      <div class="col-12">
        <div class="form-floating mb-3">
          <input type="text" class="form-control" name="accountInput" v-bind:class="{ 'is-invalid': accountErr }" v-model="account" placeholder="帳號" />
          <label for="accountInput">帳號</label>
          <div class="invalid-feedback text-end">{{ accountErrMsg }}</div>
        </div>
        <div class="form-floating mb-3">
          <input
            type="current-password"
            class="form-control"
            name="passwordInput"
            v-bind:class="{ 'is-invalid': passwordErr }"
            v-model="password"
            placeholder="密碼"
          />
          <label for="passwordInput">密碼</label>
          <div class="invalid-feedback text-end">{{ passwordErrMsg }}</div>
        </div>
      </div>
      <div class="col-8 text-start"></div>
      <div class="col-4 text-end">
        <p @click="$router.push('/register')">註冊帳戶！</p>
      </div>
      <div class="col-12 d-grid gap-2">
        <button class="btn btn-success btn-lg" type="button" @click="login">登入</button>
      </div>
    </form>
  </div>
</template>

<script>
import Alert from "@/components/Alert.vue";
import Loader from "@/components/Loading.vue";
import { jwtDecode } from "jwt-decode";

export default {
  name: "Login",
  components: {
    Alert,
    Loader,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 訊息功能碼
      funcNum: 0,
      endTime: new Date(),
      timeCount: {
        days: "00",
        hours: "00",
        minutes: "00",
        seconds: "00",
      },
      account: "",
      accountErr: false,
      accountErrMsg: "",
      password: "",
      passwordErr: false,
      passwordErrMsg: "",
    };
  },
  methods: {
    count() {
      let des = this.$store.state.countDown - new Date();
      let days = Math.floor(des / (24 * 60 * 60 * 1000));
      let hours = Math.floor((des % (24 * 60 * 60 * 1000)) / (60 * 60 * 1000));
      let minutes = Math.floor((des % (60 * 60 * 1000)) / (60 * 1000));
      let seconds = Math.floor((des % (60 * 1000)) / 1000);
      days = days < 10 ? "0" + days : days;
      hours = hours < 10 ? "0" + hours : hours;
      minutes = minutes < 10 ? "0" + minutes : minutes;
      seconds = seconds < 10 ? "0" + seconds : seconds;
      this.timeCount = {
        days: days,
        hours: hours,
        minutes: minutes,
        seconds: seconds,
      };
      if (des <= 0) {
        clearInterval(this.timer);
        this.timeCount = {
          days: "00",
          hours: "00",
          minutes: "00",
          seconds: "00",
        };
      }
    },
    login() {
      if (this.account.trim() === "") {
        this.accountErr = true;
        this.accountErrMsg = "請輸入帳號";
      }
      if (this.password.trim() === "") {
        this.passwordErr = true;
        this.passwordErrMsg = "請輸入密碼";
      }
      if (!this.accountErr && !this.passwordErr) {
        this.$http
          .post("api/users/login", { account: this.account, password: this.password })
          .then((res) => {
            if (res.data.code === 200) {
              // 登入成功，將token存入localStorage
              localStorage.setItem("eleToken", res.data.token);
              // 解析token，取得使用者資料
              const decode = jwtDecode(res.data.token);
              const user = {
                studentID: decode.studentID,
                account: decode.account,
                name: decode.name,
                ident: decode.ident,
                examExp: decode.exam_exp,
              };
              // 基本資料存入Vuex
              this.$store.commit("SET_ACCOUNT", user);
              this.alertVisible = true;
              this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
              this.funcNum = 1;
              this.MSG.TYPE = "primary"; // primary成功, warning警告, danger錯誤
              this.MSG.TITLE = "系統提示"; // 系統提示，系統警告，系統錯誤
              this.MSG.TEXT = [];
              this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
            } else {
              this.alertVisible = true;
              this.MSG.MODE = "OKOnly";
              this.funcNum = -1;
              this.MSG.TYPE = "danger";
              this.MSG.TITLE = "系統錯誤";
              this.MSG.TEXT = [];
              this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
              if (res.data.sys !== "") this.MSG.TEXT.push(res.data.sys);
            }
          })
          .catch((err) => {
            this.alertVisible = true;
            this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
            this.funcNum = -1;
            this.MSG.TYPE = "danger"; // primary成功, warning警告, danger錯誤
            this.MSG.TITLE = "系統錯誤"; // 系統提示，系統警告，系統錯誤
            this.MSG.TEXT = [];
            this.MSG.TEXT.push(err);
          });
      }
    },
    hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },
  },
  created() {
    // 如果this.$store.state.countDown為空值，則取得倒數時間
    if (this.$store.state.countDown === "") {
      // 取得倒數時間
      this.$http.get("api/systemManage/getCountDown").then((res) => {
        if (res.data.code === 200) {
          this.$store.commit("SET_COUNT_DOWN", new Date(res.data.time));
        } else {
          this.alertVisible = true;
          this.MSG.MODE = "OKOnly";
          this.funcNum = -1;
          this.MSG.TYPE = "danger";
          this.MSG.TITLE = "系統錯誤";
          this.MSG.TEXT = [];
          this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
          if (res.data.sys !== "") this.MSG.TEXT.push(res.data.sys);
        }
      });
    }
  },
  mounted() {
    this.count();
    // 執行倒數計時
    this.timer = setInterval(() => {
      this.count();
    }, 1000);
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  computed: {
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return;
          } else if (this.funcNum === -1) {
            this.hideAlert();
          } else if (this.funcNum === -2) {
            this.hideAlert();
            this.$router.go(-1);
          } else if (this.funcNum === 1) {
            // 功能1-[登入處理]
            // 頁面處理
            if (this.$store.state.account.ident === "Admin") {
              this.hideAlert();
              this.$router.push("/systemManage");
            } else {
              this.hideAlert();
              this.$router.push("/examLobby");
            }
          }
        } else if (val.OKorCancel === "Cancel") {
          this.hideAlert();
        }
      }
    },
    account: function () {
      if (this.account.trim() === "") {
        this.accountErr = true;
        this.accountErrMsg = "請輸入帳號";
      } else {
        this.accountErr = false;
      }
    },
    password: function () {
      if (this.password.trim() === "") {
        this.passwordErr = true;
        this.passwordErrMsg = "請輸入密碼";
      } else {
        this.passwordErr = false;
      }
    },
  },
};
</script>

<style lang="scss">
.text {
  font-size: 40px;
  font-weight: 900;
  text-align: center;
  position: relative;
}

.text::after {
  content: "";
  position: absolute;
  width: 36px;
  height: 14px;
  background-color: #e74c3c;
  border-radius: 10px;
  bottom: 10px;
  left: -10px;
  animation: trans 1.1s linear infinite;
  opacity: 0;
}
@keyframes trans {
  40% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    left: calc(100% + 18px);
  }
}

.text span {
  display: inline-block;
  animation: wave 1.1s linear infinite;
}
.text span:nth-child(2) {
  animation-delay: 100ms;
}
.text span:nth-child(3) {
  animation-delay: 200ms;
}
.text span:nth-child(4) {
  animation-delay: 300ms;
}
@keyframes wave {
  50% {
    transform: translateY(-15px);
  }
}
.time {
  text-transform: uppercase;
  width: 100%;
  display: flex;
  justify-content: center;
}
.time span {
  padding: 0 14px;
  font-size: 15px;
  font-weight: 900;
  margin: 0 10px;
  text-align: center;
  position: relative;
}
.time span div {
  font-size: 30px;
}
</style>
